.editorBody {
    margin-top: 10px;
    display: flex;
    justify-content: space-evenly;
}

.editorWrapper {
    outline: 2px solid red;
    width: 65vw;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    
}


#editorjs {
    display: flex;
    flex-direction: column;
    width: calc(100%);
    padding-top: 10px;
    padding-bottom: 10px;
    height: 100%;
    padding-bottom: 20px;
}

.codex-editor__redactor {
    padding-bottom: 50px !important;

}

.ce-block__content, .ce-toolbar__content {
    width: 100%;
}

.codex-editor {
    height: 100%;

}

@media (max-width: 649px) {
    .ce-block:hover {
        margin-bottom: 3em;
    }
}

@media (max-width: 900px) {
    .editorWrapper {
        width: 95vw;
    }
    
}

.hoverEffect:hover{
    border: 2px dashed rgba(73, 182, 255, .5);
    background-color: rgba(73, 172, 255, .025);
}

.drag-overlay {
    display: flex;
    justify-content: space-between;
    width: calc(100% - 40px);
    background-color: #f0f0f0; 
    padding: 15px 20px;
    border-radius: 5px;
}


